export default {
    name:'GoodDetails',
    data() {  
        return {
            
        }
    },
    components: {
        
    },
    beforeCreate(){
        this.$store.dispatch('CategoryTwoOrThreeListDetail', this.$route.query.id)
    },
    computed:{
        CategoryTwoOrThreeListDetail(){
            return this.$store.state.CategoryTwoOrThreeListDetail
        },
        htmls(){
            return this.$store.getters.htmls
        },
        imgUrlGoods(){
            return this.$store.state.imgUrlGoods
        },
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '商品详情'
    },
    activated(){
        // console.log('hah')
    },
    methods:{
        onClickMiniBtn() {
            this.CategoryTwoOrThreeListDetail.inventory == 0 ? this.isInventory() : this.$store.dispatch('insertGoods', this.$route.query.id)
        },
        onClickBigBtn() {
            this.CategoryTwoOrThreeListDetail.inventory == 0 ? this.isInventory() : this.$router.push({path:'/CreateGoods',query:{id: this.CategoryTwoOrThreeListDetail.id}})
        },
        onClickKeBtn(){
            window.location.href = 'tel:0512-66831208'
        },
        goodsCart(){
            this.$router.push('/ShoppingCart')
            this.$store.commit('ACTIVE', 3)
        },
        isInventory(){
            this.$toast('已售罄')
        }
    },
    deactivated(){
        // this.$destroy();//销毁B的实例
    }
    // beforeRouteLeave(to, from, next) {   
    // // 设置下一个路由的 meta
    //     to.path === '/GoodStatus' ? to.meta.keepAlive = true : null // B 跳转到 A 时，让 A 缓存，即不刷新（代码写在B页面）
    //     next();
    // }
}



